import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';

import { StoresService } from './stores.service';

@Component({
  selector: 'app-stores',
  templateUrl: './stores.component.html',
  styleUrls: ['./stores.component.scss'],
})
export class StoresComponent implements OnInit {
  items = [];
  modalRef: any;
  reloadItems($event) {}
  constructor(
    private storesService: StoresService,
    private modalService: BsModalService,
    private toastr: ToastrService
  ) {}

  ngOnInit(): void {
    this.storesService.getAll();

    this.storesService.stores.subscribe((data) => {
      if (data) {
        this.items = data;
      }
    });
  }

  itemId;
  openModal(template: TemplateRef<any>, id) {
    this.itemId = id;
    this.modalRef = this.modalService.show(template);
  }

  deleteElement() {
    this.modalRef.hide();
    this.storesService.deleteOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.storesService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  lockElement() {
    this.modalRef.hide();
    this.storesService.lockOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.storesService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  unlockElement() {
    this.modalRef.hide();
    this.storesService.unlockOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.storesService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  CollectElement() {
    this.modalRef.hide();
    // this.storesService.deleteOne(this.itemId).subscribe(
    //   (res) => {
    //     console.log(res);
    //     this.toastr.info(res['message'], 'Info');
    //     this.storesService.getAll();
    //   },
    //   (error) => {
    //     console.log(error);
    //     this.toastr.error(error.error.message, 'Error');
    //     // this.error.next(error.error.message);
    //   }
    // );
  }

  activateElement() {
    this.modalRef.hide();
    this.storesService.activateOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.storesService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  deactivateElement() {
    this.modalRef.hide();
    this.storesService.deactivateOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.storesService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }
}
